@import '../../Styles/variables';
.bag {
	gap: 5%;
	position: fixed;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	background-color: #000;
	background-color: rgba(0, 0, 0, 0.5);
	overflow: hidden;
	z-index: 10;
}
.container {
	background-color: #fff;
	position: absolute;
	top: 0;
	right: 0;
	height: 100%;
	width: 27%;
	background-color: #fff;
}
.wrapper {
	display: flex;
	height: 100%;
	flex-direction: column;
	padding-left: 10%;
	padding-right: 10%;
}
.header {
	display: flex;
	justify-content: space-between;
	@extend %font-family;
	font-weight: 700;
	font-size: 24px;
	padding-top: 10%;
	padding-bottom: 10%;
	button {
		@extend %small-btn;
		width: 40px;
		height: 40px;
		svg {
			fill: $small-text;
		}
		&:hover {
			background-color: #ffd0d0;
		}
	}
}
.content {
	flex: 50%;
	overflow: auto;
	display: flex;
	gap: 5%;
	flex-direction: column;
}
.footer {
	flex: 0 0 auto;
	padding-top: 10%;
	padding-bottom: 10%;
}

.cheque {
	@extend %font-family;
	font-weight: 400;
	font-size: 20px;
	& li {
		display: flex;
		&:not(:last-child) {
			margin-bottom: 20px;
		}
	}
}
.space {
	flex-grow: 1;
	align-self: center;
	height: 1px;
	border-bottom: 1px dashed $border-color;
	margin: 0 10px;
}
.coast {
	font-weight: 600;
}
@media screen and (min-width: 1400px) and (max-width: 1955px) {
	.container {
		width: 30%;
	}
	.wrapper {
		padding-left: 5%;
		padding-right: 5%;
	}
}

@media screen and (min-width: 1024px) and (max-width: 1400px) {
	.container {
		width: 40%;
	}
}
@media screen and (min-width: 768px) and (max-width: 1024px) {
	.container {
		width: 50%;
	}
}
@media screen and (max-width: 768px) {
	.container {
		width: 100%;
	}
}
