@import '../../Styles/variables';

.searchBar {
	z-index: 100;
	background: #fff;
}
.container {
	display: flex;
	border: 1px solid $border-color;
	align-items: center;
	padding: 15px;
	border-radius: 10px;
	&:focus-within {
		border: 1px solid #000;
	}
}
.icon {
	margin-right: 15px;
	stroke: #e4e4e4;
}
.input {
	border: none;
	@extend %font-family;
	font-weight: 400;
	font-size: 16px;
	color: #000;
	&::placeholder {
		color: $small-text;
	}
	outline: none;
}
@media screen and (max-width: 642px) {
	.container {
		border: none;
	}
	.searchBar {
		border-radius: 20px;
	}
